import { Box, Heading, Image, Flex } from "@chakra-ui/react";

const Header = () => {
  return (
    <Box textAlign="center" p={6} bg="transparent" borderRadius="xl">
      <Heading
        fontSize={{ base: "3xl", md: "6xl" }}
        fontWeight="extrabold"
        bgGradient="linear(to-r, MediumBlue.500, cyan.400)"
        bgClip="text"
        mb={6}
      >
        Свадьба Игоря и Виктории
      </Heading>
      <Flex
        direction={{ base: "column", md: "row" }}
        align="center"
        justify="center"
        gap={6}
        mt={6}
      >
        <Image
          src="https://raw.githubusercontent.com/Volking-Sourse/WendingDay/refs/heads/main/public/bride.jpg"
          alt="Невеста"
          w="300px"
          borderRadius="md"
          boxShadow="md"
        />
        <Image
          src="https://raw.githubusercontent.com/Volking-Sourse/WendingDay/refs/heads/main/public/groom.jpg"
          alt="Жених"
          w="300px"
          borderRadius="md"
          boxShadow="md"
        />
      </Flex>
    </Box>
  );
};

export default Header;