import React from "react";
import { ChakraProvider, Box } from "@chakra-ui/react";
import { motion } from "framer-motion";

// Компонент анимации появления
const FadeIn = ({ children }) => (
  <motion.div
    initial={{ opacity: 0, y: 20 }}
    whileInView={{ opacity: 1, y: 0 }}
    viewport={{ once: true }}
    transition={{ duration: 0.8 }}
  >
    {children}
  </motion.div>
);

function App() {
  return (
    <ChakraProvider>
      {/* Обёртка с бледно-голубым фоном */}
      <Box bg="#f0fdfd" minH="100vh" w="100%" p="2rem">
        <Header />
        <FadeIn>
          <WelcomeSection />
        </FadeIn>
        <FadeIn>
          <Countdown />
        </FadeIn>
        <FadeIn>
          <WeddingDateSection />
        </FadeIn>
        <FadeIn>
          <LocationSection />
        </FadeIn>
        <Footer />
      </Box>
    </ChakraProvider>
  );
}

export default App;

// Эти компоненты должны быть импортированы из твоих файлов:
import Header from "./components/Header";
import WelcomeSection from "./components/WelcomeSection";
import WeddingDateSection from "./components/WeddingDateSection";
import LocationSection from "./components/LocationSection";
import Footer from "./components/Footer";
import Countdown from "./components/Countdown";